<template>
  <div class="center questionnaireLibrary">
      <div class="center_breadcrumb">
        <crumb>
            <div @click="openAdd" class="new--btn" slot="right">+ 新增</div>
        </crumb>
    </div>
    <div class="center_content">
        <div class="ability-query_side">
                    <h3>问卷类型</h3>
                    <ul>
                        <li
                         @click="tabMenu(index, vo.id)" v-for="(vo, index) in sideMenu" :key="index" :class="{'sideActive': activeNum === index}">
                         <span>{{vo.classifyName}}</span>
                        </li>
                    </ul>
                </div>
                <div class="questionnaireLibrary-right">
                        <div class="center_content_search">
                            <div class="center_content_search_left">
                                <div class="search-input-suffix">
                                    <label>问卷名称：</label>
                                    <el-input class="search"
                                            @keyup.enter.native="search"
                                            v-model="page.keyWord"
                                            placeholder="请输入搜索关键字"
                                            >
                                    </el-input>
                                </div>
                            <div class="search--btn" style="margin: 0 0 0 20px" @click="search">搜索</div>
                            </div>
                        </div>
                        <el-table :data="list"  stripe style="width: 100%; padding-bottom:40px;border-radius: 10px;margin-bottom: 110px;">
                           <el-table-column label="问卷名称"   :show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    {{ scope.row.formName}} 
                                </template>
                            </el-table-column>
                            <el-table-column label="引用次数" >
                                <template slot-scope="scope">
                                    <span>{{ scope.row.refCount }}</span>
                                </template>
                            </el-table-column>
                            <el-table-column label="操作" width="220">
                                <template slot-scope="scope">
                                    <el-button type="text" size="small" @click="openPreview(scope.row.id)">
                                        <svgIcon icon-class="iconyulan"></svgIcon>&nbsp;&nbsp;预览
                                    </el-button>
                                     <el-button type="text" size="small" @click="openQuote(scope.row)">
                                        <svgIcon icon-class="iconyinyong"></svgIcon>&nbsp;&nbsp;引用
                                    </el-button>
                                     <el-button style="color: #ff5858;" type="text" size="small" @click="discard(scope.row.id)">
                                        <svgIcon icon-class="iconshanchu"></svgIcon>&nbsp;&nbsp;废弃
                                    </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                </div>
    </div>
 </div>
</template>

<script>
export default {
 name: 'QuestionnaireLibrary',
 components: {},
 data() {
 return {
    pageTitle:'问卷模板库',
    activeNum:0,
    sideMenu:[],
    list:[],
    page:{
      classifyId:'',
      keyword:''  
    },
    api:{
        classify:'/form/rest/FormClassifyBusiness/listAll/v1',
        templateList:'/form/rest/FormTemplateMainBaseBusiness/listAll/v1',
        discardTemp:'/form/rest/FormMainBaseBusiness/discardTemp/v1'
    }
 }
 },
 created(){
     this.classifyAll()
 },
 mounted(){},
 methods:{
     classifyAll(){
       this.$post(this.api.classify).then(res=>{
           if(res.code ===200){
                this.sideMenu=res.data
                this.page.classifyId=res.data[0].id
                this.initData()
           }
       })
     },
     initData(){
         this.$post(this.api.templateList, this.page).then(res =>{
             if(res.code ===200){
                 res.data.forEach(item =>{
                     item.off=true
                     item.on=true
                 })
                 this.list= res.data
             }
         })
     },
     search(){
         this.initData()
     },
     tabMenu(index, id){
        this.activeNum=index 
        this.page.classifyId=id
        this.initData()
     },
     openPreview(id){
         this.$router.push({
             path:'/evaluationAndFeedback/questionnairePreview',
             query:{
                 id:id,
                 entryType:'template'
             }
         })
     },
     openQuote(vo){
        this.$router.push({
             path:'/evaluationAndFeedback/addQuestionnaire',
             query:{
                 id:vo.id,
                 classifyId:vo.classifyId,
                 title:vo.formName,
                 entryType:'copyTemplate',
             }
         })
     },
      discard(id){
        this.$post(this.api.discardTemp, {id:id}).then(res =>{
             if(res.code ===200){
                this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '操作成功'                     
                    })

                this.initData()
             }else{
                 this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.msg
               }) 
             }
         })
     },
     enter(type, index, off){
        if(type === 'preview'){
             this.list[index].off= !off
         }else{
            this.list[index].on= !off
         }
       
     },
     leave(type, index, off){
     if(type === 'preview'){
             this.list[index].off= !off
         }else{
            this.list[index].on= !off
         }
     },
     openAdd(){         
        this.$router.push({
             path:'/evaluationAndFeedback/addQuestionnaire',
             query:{
                entryType:'template'
             }
        })
     }
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss';
  .questionnaireLibrary{
        .active{
            background-color: #5f77ff;
            color: #fff;
            border-radius: 4px;
            border:0;
        }
        .ability-query_side{
            width:260px;
            min-height:100%;
            overflow-y: auto;
            background:#fff;
            border-radius:10px;
            float:left;
            padding-bottom:80px;
            h3{
               line-height:80px;
               text-align:center;
                color:#323232;
                font-size:20px;
            }
            ul{
                li{
                    height:60px;  
                    width:100%;      
                    position:relative;   
                    background:#fff;    
                     cursor:pointer;  
                    span{
                        display:inline-block;
                        width:160px;
                        height:40px;
                        font-size:14px;      
                        line-height:40px;
                        text-align:center;
                        background:#fff;
                        color:#696969;
                        border-radius:20px;
                        white-space: nowrap;     
                        overflow: hidden;         
                        text-overflow: ellipsis;  
                        padding:0 20px;             
                         position:absolute;
                         left:0;
                         top:0;
                         right:0;
                         bottom:0;
                         margin:auto;

                    }
                    &.sideActive{
                        span{
                            background:#03B187;
                            box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
                            color:#fff;
                            font-size:16px;
                            
                        }    
                    }
                }
            
            }
        }
        .questionnaireLibrary-right{
            width:calc(100% - 300px);
            overflow-y: auto;
            float:right;
            margin-right:20px;
        }
        .el-pagination{
            margin:20px 0;
            float: right;
        }
        
    }

</style>




